/* Home.css */

/* CSS Grid for dynamic layout */
.project-content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Adjust the column width dynamically */
    grid-gap: 20px; /* Add gap between grid items */
    padding: 20px; /* Add padding to the grid container */
    margin-left: 15px;
  }
  